.modal {
  position: fixed;
  display: grid;
  grid-template-areas: "banner" "propuesta" "vision" "unete";
  grid-template-rows: 15% min-content min-content min-content;
  grid-template-columns: 1fr;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
  transform: scale3d(0, 0, 0);
  background-color: white;
  overflow-y: auto; }
  .modal.zoom-in {
    animation: zoomIn .5s forwards; }

@keyframes zoomIn {
  to {
    transform: scale3d(1, 1, 1); } }
  .modal.zoom-out {
    animation: zoomOut .5s forwards; }

@keyframes zoomOut {
  from {
    transform: scale3d(1, 1, 1); }
  to {
    transform: scale3d(0, 0, 0); } }
  .modal-banner {
    grid-area: banner;
    display: flex;
    flex-wrap: wrap;
