  //Sombras

  //box-shadow = ejeX ejeY difuminado(ejemplo 5px) color

  $shadow-flat: 7px 7px 12px rgba(0, 0, 0, 0.575);
  $shadow-blur: 10px 10px 10px rgba(0, 0, 0, 0.575);

  .sombra-curva{
    position: relative;
    &:after{
      content: "";
      position: absolute;
      width: 96%;
      height: 10%;
      bottom: -1%;
      left: 0;
      z-index: -1;
      border-radius: 50%;
      background-color: #000;
      //se puede jugar con el opacity
      opacity: .3;
      box-shadow: 0 5px 12px #000;
    }
  }



  .sombra-concava{
    position: relative;
    &:after,
    &:before{
      content: "";
      position: absolute;
      width: 96%;
      height: 5%;
      bottom: 3%;
      z-index: -1;
      background-color: #000;
      box-shadow: 0 5px 12px black;
    }
    &:before{
      left: 1%;
      transform: rotate(-2deg);
    }
    &:after{
      right: 1%;
      transform: rotate(2deg);
    }
  }
